.mi-anim-fade-enter-active,
.mi-anim-fade-leave-active {
  opacity: 1;
}
.mi-anim-fade-enter-from,
.mi-anim-fade-leave-to {
  opacity: 0;
}
.mi-anim-scale-enter-active,
.mi-anim-scale-leave-active {
  opacity: 1;
  transform: scale(1);
}
.mi-anim-scale-enter-from,
.mi-anim-scale-leave-to {
  transform: scale(0);
  opacity: 0;
}
.mi-anim-slide-enter-active,
.mi-anim-slide-leave-active,
.mi-anim-slide-right-enter-active,
.mi-anim-slide-right-leave-active {
  opacity: 1;
  transform: translateX(0);
}
.mi-anim-slide-enter-from,
.mi-anim-slide-leave-to,
.mi-anim-slide-right-enter-from,
.mi-anim-slide-right-leave-to {
  transform: translateX(20%);
  opacity: 0;
}
.mi-anim-slide-bottom-enter-active,
.mi-anim-slide-bottom-leave-active {
  opacity: 1;
  transform: translateY(0);
}
.mi-anim-slide-bottom-enter-from,
.mi-anim-slide-bottom-leave-to {
  transform: translateY(20%);
  opacity: 0;
}
.mi-anim-slide-fall-enter-active,
.mi-anim-slide-fall-leave-active {
  opacity: 1;
  transform: translate(0%) translateZ(0) rotate(0deg);
}
.mi-anim-slide-fall-enter-from,
.mi-anim-slide-fall-leave-to {
  transform: translate(30%) translateZ(660px) rotate(30deg);
  opacity: 0;
}
.mi-anim-newspaper-enter-active,
.mi-anim-newspaper-leave-active {
  opacity: 1;
  transform: scale(1) rotate(0deg);
}
.mi-anim-newspaper-enter-from,
.mi-anim-newspaper-leave-to {
  transform-style: preserve-3d;
  transform: scale(0) rotate(720deg);
  opacity: 0;
}
.mi-anim-sticky-enter-active,
.mi-anim-sticky-leave-active {
  opacity: 1;
  transform: translateY(0);
}
.mi-anim-sticky-enter-from,
.mi-anim-sticky-leave-to {
  transform: translateY(-200%);
  opacity: 0;
}
.mi-anim-flip-enter-active,
.mi-anim-flip-leave-active,
.mi-anim-flip-horizontal-enter-active,
.mi-anim-flip-horizontal-leave-active {
  opacity: 1;
  transform: rotateY(0deg);
}
.mi-anim-flip-enter-from,
.mi-anim-flip-leave-to,
.mi-anim-flip-horizontal-enter-from,
.mi-anim-flip-horizontal-leave-to {
  transform-style: preserve-3d;
  transform: rotateY(-70deg);
  opacity: 0;
}
.mi-anim-flip-vertical-enter-active,
.mi-anim-flip-vertical-leave-active {
  opacity: 1;
  transform: rotateX(0deg);
}
.mi-anim-flip-vertical-enter-from,
.mi-anim-flip-vertical-leave-to {
  transform-style: preserve-3d;
  transform: rotateX(-70deg);
  opacity: 0;
}
.mi-anim-fall-enter-active,
.mi-anim-fall-leave-active {
  opacity: 1;
  transform: translateZ(0px) rotateX(0deg);
}
.mi-anim-fall-enter-from,
.mi-anim-fall-leave-to {
  transform: translateZ(660px) rotateX(20deg);
  opacity: 0;
}
.mi-anim-rotate-enter-active,
.mi-anim-rotate-leave-active {
  transform: translateY(0%) rotateX(0deg);
  opacity: 1;
}
.mi-anim-rotate-enter-from,
.mi-anim-rotate-leave-to {
  transform-style: preserve-3d;
  transform: translateY(100%) rotateX(90deg);
  transform-origin: 0 100%;
  opacity: 0;
}
.mi-anim-sign-enter-active,
.mi-anim-sign-leave-active {
  transform: rotateX(0deg);
  opacity: 1;
}
.mi-anim-sign-enter-from,
.mi-anim-sign-leave-to {
  transform-style: preserve-3d;
  transform: rotateX(-90deg);
  transform-origin: 50% 0;
  opacity: 0;
}
.mi-anim-slit-enter-active {
  -webkit-animation: mi-anim-slit 0.4s forwards ease-out;
  animation: mi-anim-slit 0.4s forwards ease-out;
}
.mi-anim-slit-leave-active {
  animation: mi-anim-slit 0.4s forwards ease-out reverse;
}
.mi-anim-shake-enter-active {
  -webkit-animation: mi-anim-modal-shake 0.4s forwards linear;
  animation: mi-anim-modal-shake 0.4s forwards linear;
}
.mi-anim-shake-leave-active {
  animation: mi-anim-modal-shake 0.4s forwards linear reverse;
}
.mi-anim-page-slide-enter-active,
.mi-anim-page-slide-leave-active {
  transition: all 0.4s linear;
}
.mi-anim-page-slide-leave-from,
.mi-anim-page-slide-enter-to {
  transform: translateX(0);
  opacity: 1;
}
.mi-anim-page-slide-enter-from {
  transform: translateX(100%);
  opacity: 0;
}
.mi-anim-page-slide-leave-to {
  transform: translateX(-100%);
  opacity: 0;
}
.mi-anim-breadcrumb-enter-active,
.mi-anim-breadcrumb-leave-active {
  transition: all 0.4s linear;
}
.mi-anim-breadcrumb-leave-from,
.mi-anim-breadcrumb-enter-to {
  transform: translateX(0);
  opacity: 1;
}
.mi-anim-breadcrumb-enter-from {
  transform: translateX(-32px);
  opacity: 0;
}
.mi-anim-breadcrumb-leave-to {
  transform: translateX(32px);
  opacity: 0;
}
.mi-anim-anchor-enter-active,
.mi-anim-anchor-leave-active {
  opacity: 1;
  transform: translateX(0);
}
.mi-anim-anchor-enter-from,
.mi-anim-anchor-leave-to {
  opacity: 0;
  transform: translateX(5rem);
}
